import { CacheType, IAction, IActionContext, IActionInput, ICreateActionContext } from '@msdyn365-commerce/core-internal';
import { createObservableDataAction } from '../../create-data-action.vnext';

/**
 * test class
 */
/*@internal*/
export class GetActionTimeoutInput implements IActionInput {
    public timeout: number;

    constructor(timeout: number) {
        this.timeout = timeout;
    }

    public dataCacheType = (): CacheType => 'application';
    public getCacheObjectType = () => 'Timeout';
    public getCacheKey = () => `${this.timeout}`;
}

async function GetActionTimeout(input: GetActionTimeoutInput, ctx: IActionContext): Promise<number> {
    ctx.trace('Starting action timeout with delay of ' + input.timeout);
    return new Promise(resolve => {
        setTimeout(resolve.bind(resolve, input.timeout), input.timeout);
    });
}

const createTestInput = (arg: ICreateActionContext<any>): IActionInput => {
    return new GetActionTimeoutInput((arg.config && arg.config.timeout) || 0);
};

/*@internal*/
export default createObservableDataAction({
    action: GetActionTimeout as IAction<number>,
    input: createTestInput
});
